.winScreen {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-animation: in .7s ease;
            animation: in .7s ease;
}
.winScreen__title {
    font-size: 40px;
    margin-top: 0;
    margin-bottom: 20px;
}
.winScreen__description {
    font-size: 30px;
    margin-bottom: 20px;
}
.winScreen__restartBtn {
    font-size: 30px;
    line-height: 25px;
    border: 2px solid;
    border-radius: 10px;
    padding: 5px;
    cursor: pointer;
    -webkit-transition: .2s;
    -o-transition: .2s;
    transition: .2s;
}
.winScreen__restartBtn:hover {
    background-color: black;
    color: #fff;
}

@-webkit-keyframes in {
    from {
        -webkit-transform: scale(.5);
                transform: scale(.5);
        opacity: 0;
    }
    to {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
}

@keyframes in {
    from {
        -webkit-transform: scale(.5);
                transform: scale(.5);
        opacity: 0;
    }
    to {
        -webkit-transform: scale(1);
                transform: scale(1);
        opacity: 1;
    }
}


