.tile {
    width: 115px;
    height: 115px;
    position: relative;
    font-size: 70px;
    -webkit-perspective: 1000px;
            perspective: 1000px;
    -webkit-user-select: none;
       -moz-user-select: none;
        -ms-user-select: none;
            user-select: none;
    cursor: pointer;
}
.tile__inner {
    width: 100%;
    height: 100%;
    position: absolute;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
    border: 1px solid black;
    border-radius: 8px;
    -webkit-transform-style: preserve-3d;
            transform-style: preserve-3d;
}
.tile__front {
    width: 100%;
    height: 100%;
    position: absolute;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border-radius: 8px;
}
.tile__back {
    width: 100%;
    height: 100%;
    position: absolute;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
    border-radius: 8px;
}
.is-open {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
.is-find {
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}


